<script>import Icon from "svelte-fa";
import { faCheckSquare } from "@fortawesome/free-solid-svg-icons/faCheckSquare";
import { getComponentForType } from "../helpers/filetype";
export let dataPoint;
export let symphonySpec;
export let selected;
export let tooltip;
export let number = undefined;
$: id = dataPoint[$symphonySpec.idColumn];
let hover = false;
let m = { x: 0, y: 0 };
function select() {
    const index = $selected.indexOf(id);
    if (index > -1) {
        selected.update((d) => {
            d.splice(index, 1);
            return [...d];
        });
    }
    else {
        selected.set([...$selected, id]);
    }
}
function handleMousemove(event) {
    m.x = event.clientX;
    m.y = event.clientY;
    updateSampleTooltip();
}
function updateSampleTooltip() {
    tooltip.set({
        hover: hover,
        mousePos: m,
        fetchPrefix: $symphonySpec.filesPath,
        instance: dataPoint,
        number: number,
    });
}
</script>

<div
  on:mouseenter={() => {
    hover = true;
    updateSampleTooltip();
  }}
  on:mouseleave={() => {
    hover = false;
    updateSampleTooltip();
  }}
  on:mousemove={handleMousemove}
  class="relative"
>
  <svelte:component
    this={getComponentForType($symphonySpec.dataType)}
    {id}
    {symphonySpec}
    on:click={() => select()}
  />
  {#if $selected.includes(id)}
    <Icon
      class="absolute top-0 right-0 bg-white border-2 border-white"
      icon={faCheckSquare}
    />
  {/if}
</div>
